.Intro {
    padding: 100px 0 145px 15px;
    max-width: 100%;
}

.Intro_content {
    padding: 95px 0;
}

.Intro_content h1 {
    color: #fff;
    margin-bottom: 30px;
    font-family: "Work Sans", sans-serif;
    font-size: 40px;
    font-weight: 300;
}

.Intro_content input[type="email"] {
    border: none;
    padding: 9px 10px;
    border-radius: 6px 0 0 6px;
    font-size: 16px;
    min-width: 60%;
    vertical-align: middle;
}

.Intro_content label {
    margin-bottom: 10px;
    font-weight: 300;
    color: #dadada;
    font-size: 18px;
}

.Intro_content .btn {
    border: none;
    border-radius: 0 6px 6px 0;
    background: #49c3cf;
    color: #fff;
    padding: 10px 20px;
    text-transform: uppercase;
    font-weight: 400;
    font-size: 14px;
    max-width: 39%;
    vertical-align: middle;
}

.Intro_image {
    padding-right: 0;
}

@media (max-width: 1023px) {
    .Intro {
        padding: 45px 0 45px 15px;
        width: 100%;
    }

    .Intro_content {
        padding: 0 0 100px;
        text-align: center !important;
    }
}

@media (min-width: 960px) {
    .Intro {
        position: static;
    }

    .Intro_image {
        position: static;
    }

    .Intro_image img {
        position: absolute;
        right: 0;
    }
}

@media (min-width: 1024px) and (max-width: 1199px) {
    .Intro_image img {
        position: absolute;
        right: 0;
        top: 150px;
        max-width: 450px;
    }
}

@media (min-width: 768px) and (max-width: 959px) {
    .Intro_content {
        padding: 0 !important;
        text-align: left !important;
    }
    .Intro_content h1 {
        font-size: 28px;
    }
}



